body {
  overflow: hidden;
}

body.loading {
  cursor: progress;
}

.controls {
  position: absolute;
  top: 0;
  left: 0;
}

.hidden {
  display: none;
}

#dropZone {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
}

/* Adding the text this way avoids losing event target,
   which was a problem when dragging over text-element inside #dropZone */
#dropZone:after {
  content: '\2913 \A Bild hinzuf√ºgen';
  white-space: pre;
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 5rem;
  font-weight: bold;
  color: white;
  line-height: 1;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}

#uploadedImage {
  position: absolute;
  cursor: move;
}

#colorPickerTooltip {
  position: absolute;
  width: 50px;
  height: 30px;
}

#debugTooltip {
  position: absolute;
}

